<template>
    <div class="d-flex">
        ealuasi
        {{ AppStore().appCacheData[`pembelajaran`]
            .evaluasi[
            `${AppStore().appCacheData[`${AppStore().activeContent.data.name}`]
                .lastSelectedSubContent
            }`
        ] }}
        <div class="col-12 table-wrapper" v-if="AppStore().appCacheData[`${AppStore().activeContent.data.name}`] &&
            Object.keys(route.query).length <= 0
            ">
            <div class="container-fluid" v-if="AppStore().appCacheData[`pembelajaran`]
                    .evaluasi[
                `${AppStore().appCacheData[`${AppStore().activeContent.data.name}`]
                    .lastSelectedSubContent
                }`
                ]
                ">

                <table
                    class="aplication-content-table table table-bordered table-hover table-sm border-secondary rounded-5">
                    <thead>
                        <tr class="table-primary text-center text-capitalize text-nowrap">
                            <th class="fw-bolder" scope="col">nomor Halaman</th>
                            <th class="fw-bolder" scope="col">nama halaman</th>
                            <th class="fw-bolder" scope="col">aksi</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="text-lowercase text-nowrap" v-for="(page, key, index) in AppStore().appCacheData[
                                `pembelajaran`
                            ].evaluasi[
                            `${AppStore().appCacheData[
                                `${AppStore().activeContent.data.name}`
                            ].lastSelectedSubContent
                            }`
                            ]" :key="key">
                            <th class="text-center" scope="row">
                                {{ Number(index) + 1 }}
                            </th>
                            <td>{{ Object(page).name }}</td>
                            <td>
                                <div class="container">
                                    <div class="row align-items-center gap-3">
                                        <button class="col-auto button-style small bg-secondary" style="width: fit-content"
                                            @click="editPage({
                                                pageNumber: Number(index) + 1,
                                                ...Object(page),
                                            })
                                                ">
                                            edit halaman
                                        </button>
                                        <button class="col-auto button-style small bg-danger text-light"
                                            style="width: fit-content" @click="deletePage(String(key), Object(page).name)">
                                            hapus
                                        </button>
                                    </div>
                                </div>
                            </td>
                        </tr>
                        <tr v-if="AppStore().appCacheData[`pembelajaran`]
                                .evaluasi[
                                `${AppStore().appCacheData[
                                    `${AppStore().activeContent.data.name}`
                                ].lastSelectedSubContent
                                }`
                            ].length <= 0
                            ">
                            <td class="text-center text-danger fw-bolder" scope="row" colspan="5">
                                Tidak ada halaman
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="container-fluid" v-else>
                <span class="d-block bg-danger text-center text-light rounded-4 p-2">tidak ada sub konten dipilih</span>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
const router = useRouter();
const route = useRoute();
const editPage = (pageData: any) => {
    if (pageData.type === "delta") {
        router.push({
            name: "dashboard-edit-content",
            query: {
                sub: String(
                    AppStore().appCacheData[`${AppStore().activeContent.data.name}`]
                        .lastSelectedSubContent
                ),
                page: pageData.pageNumber,
            },
        });
    }
};
const deletePage = (id: string, title: string) => {
    const confirmDeletePage = confirm(`Yakin menghapus halaman ${title}?`);
    if (confirmDeletePage) {
        AppStore().deletePage(id);
    }
};
</script>

<style scoped lang="scss">
.table-wrapper {
    overflow: auto;
}
</style>